import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Block } from 'jsxstyle'
import { API } from '../../Constants'
import Spinner from '../Spinner'
import store from '../Store'
import { Redirect } from 'react-router-dom'

const FileList = ({ files }) => (
  <div className="file-list">
    {files && (
      <ul className="form-list">
        {files.map((item) => (
          <li key={item.name}>&bull;&nbsp;&nbsp; {item.name}</li>
        ))}
      </ul>
    )}
  </div>
)

FileList.propTypes = {
  files: PropTypes.array
}

class Intro extends Component {

  state = {
    files: null,
    results: null,
    working: false,
    problem: false,
    error: "Please attach files to analyze",
    imageSet: "",
    done: false
  }

  clientId = () => {
    return Math.random().toString(36).slice(2);
  }

  openFileDialog = (e) => {
    var form = document.getElementById('file');
    form.click();
  }

  busy = (isBusy) => {
    this.setState({ working: (isBusy === false ? false : true) });
  }

  handleClick = (e) => {
    if (this.state.working) {
      return;
    }

    this.setState({ problem: false });
    if (this.state.files && this.state.files.length) {
      this.setState({ imageSet: this.clientId() });
      var that = this;
      this.busy();
      var data = new FormData();
      for (var i = 0; i < this.state.files.length; i++) {
        data.append("file-" + i, this.state.files[i]);
      }
      let pageType = "";
      var radios = document.getElementsByName('rd-pagetype');

      for (var i = 0, length = radios.length; i < length; i++) {
        if (radios[i].checked) {
          pageType = radios[i].value;
          break;
        }
      }

      let audienceSize = "";
      if (store.hasUser()) {
        audienceSize = "&audienceSize=" + store.user().fanCount;
      }

      fetch(API + '/page/sortimagefiles?publishEnvironment=' + pageType + audienceSize, {
        method: 'POST',
        body: data
      }).then(function (response) {
        if (response.ok) {
          return response.json();
        }
        return false;

      })
        .then(function (data) {
          if (data && data.length > 0) {
            let u = store.user() == null ? {} : store.user();
            u.imageSet = that.state.imageSet;
            store.user(u);
            store.images(u.imageSet, data);
            that.setState({ done: true, working: false, results: data });
          } else {
            that.setState({ done: false, working: false, problem: true, error: "Sorry, something went wrong, we are unable to analyze your photos!" });
          }
        }).catch(function (error) {
          console.log('request failed', error)
        });
    } else {
      this.setState({ problem: true });
    }
  }

  componentDidMount() {

  }

  handleChange = (e) => {
    var fls = [];
    for (var i = 0; i < e.target.files.length; i++) {
      let f = e.target.files[i];
      f.key = e.target.files[i].name;
      fls.push(f);
    }
    this.setState({ files: fls });
  }

  render() {
    const { files, done, working, problem, error, imageSet } = this.state
    return (
      <div className="jumbotron home">
        {done && (
          <Redirect to={"/detail/" + imageSet} />
        )}
        <img src="header@2x.png" />
        <div className="call-to-action">
          <span onClick={this.openFileDialog}>
            <i className="fa fa-plus-circle" ></i>
            &nbsp;&nbsp;Add Your Images Here</span>
          <form style={{ visibility: 'hidden', height: "1px" }} className="uploader" id="uploader" method="post" action={API + "page/sortimagefiles"} encType="multipart/form-data">
            <input type="file" id="file" multiple onChange={this.handleChange} />
          </form>
          <FileList files={files} />
          <div className="line"></div>
          <ul className="form-list">
            <li>
              <label><input type="radio" name="rd-pagetype" value="Instagram" defaultChecked /><i className="fa fa-instagram"></i> Instagram</label>
            </li>
            <li>
              <label><input type="radio" name="rd-pagetype" value="Facebook" /><i className="fa fa-facebook-official" ></i>&nbsp; Facebook</label>
            </li>
          </ul>

          <div className="line"></div>
          <div>
            <a className="btn btn-primary btn-radius gray" onClick={this.handleClick} role="button"
              style={{ color: "#fff", fontWeight: "bold", fontSize: "16px" }}>Let’s Analyze
            {working && (<Spinner inline={true} />)}
            </a>
            {problem && (<span className="problem" > {error}</span>)}
          </div>
        </div>
        <div className="home-headline">
          <h1 className="display-3">CREATE BEST CONTENT <br />INCREASE YOUR ENGAGEMENT</h1>
          <p>Pangea Likemeter, evaluates your travel and lifestyle content and predicts the likes that your post will get in Facebook
        and Instagram.</p>
          <p className="note">*Currently we can’t evaluate selfies. But we know you are beautiful</p>
        </div>
      </div>

    )
  }
}

export default Intro



