import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Spinner from './Spinner'
import { API } from '../Constants'

class Instagram extends Component {
  state = {
    user: null,
    pages: null,
    working: false
  }

  componentDidMount() {
    this.status()
  }

  status = () => {

  }

  busy = (isBusy) => {
    this.setState({ working: (isBusy === false ? false : true) });
  }

  login = () => {
    var that = this;
    //#access_token=207719237.bd52243.ab917dc3155f4261b2dfb2e42352f841
    this.busy();

    let instagramUrl = "https://www.instagram.com/oauth/authorize/?client_id=bd52243c2b244f17828d74c5e35f0ffa&redirect_uri=http://localhost:8000&response_type=token&scope=public_content";
    let authWindow = window.open(instagramUrl, 'authWindow');
    fetch("", {
      headers: {
        'Access-Control-Allow-Origin': '*',
        "Content-Type": "application/json; charset=utf-8"
      },
      mode: 'cors'

    })
      .then((response) => {
        console.log(response);
        return response.json();
      }).then((data) => {
        console.log(data);
      }).catch((err) => {
        console.log(err);
      });

    /*
    IG.login(function (response) {
      if (response.session) {
        // user is logged in
      }
    }, { scope: ['comments', 'likes'] });
    */
  }

  extendToken = (token) => {
    var that = this;
    fetch('http://onedestapidev.azurewebsites.net/configuration/extendAccessToken?appId=749679035174071&accessToken=' + token, {})
      .then(function (response) {
        return response.json();
      }).then(function (data) {
        that.setState({ extendedToken: data });
        that.pages(data);
      }).catch(function (error) {
        console.log('request failed', error)
      });
  }
  signup = (name, token, pageId, fanCount) => {
    let data = {
      "Name": "string",
      "PageId": pageId,
      "PageToken": "string",
      "PageType": 0,
      "FanCount": fanCount
    };

    fetch(API + 'user/organization', {
      method: "POST",
      body: JSON.stringify(data)
    })
      .then(function (data) {
        console.log('request succeeded with JSON response', data)
      }).catch(function (error) {
        console.log('request failed', error)
      });
  }

  logout = () => {
    FB.logout(function (response) { });
  }

  pages = (longToken) => {
    var that = this;
    FB.api('me/accounts', 'get', { access_token: longToken, fields: 'picture,fan_count,name,category,access_token' }, function (accountsResponse) {
      if (accountsResponse.data) {
        that.setState({ pages: accountsResponse.data });
        console.log(that.state.pages);
      }
    });
  }

  render() {
    const { pages, working } = this.state;
    return (
      <div style={{ textAlign: "center" }} >
        <button className="btn btn-primary btn-radius ins-connect" onClick={this.login} >
          <i className="fa fa-instagram"></i>&nbsp;&nbsp;&nbsp;&nbsp;CONNECT WITH INSTAGRAM</button> <br />
        {working && (<Spinner />)}
      </div>
    )
  }
}
export default Instagram



