import React, { Component } from 'react'
import Header from './Header'
import Analyze from './Analyze'
import Footer from '../Footer'
import Results from '../Results'
import store from '../Store'
import { API } from '../../Constants'
import { Redirect } from 'react-router-dom'

class Detail extends Component {

  constructor(props) {
    super(props);
  }

  state = {
    working: false,
    problem: false,
    error: "Please attach files to analyze",
    imageSet: "",
    reprocessed: false
  }

  reprocess = (imageSet) => {
    let that = this;
    let images = store.images(imageSet), urls = [];
    if (images == null || images.length == 0) {
      return;
    }

    for (let i = 0; i < images.length; i++) {
      let img = images[i];
      urls.push(img.Url);
    }
    this.setState({ working: true });

    let data = {
      "Urls": urls,
      "PublishEnvironment": "Instagram" //TODO: Decide by login
    }

    if (store.hasUser()) {
      data["AudienceSize"] = store.user().fanCount;
    }

    fetch(API + '/page/sortimagesurls', {
      method: 'POST',
      headers: {
        "Content-Type": "application/json; charset=utf-8"
      },
      body: JSON.stringify(data)
    }).then(function (response) {
      if (response.ok) {
        return response.json();
      }
      return false;
    }).then(function (data) {
      //console.log(that.clientId(), data);
      if (data && data.length > 0) {
        let imageSet = that.clientId();
        store.images(imageSet, data);
        that.setState({ reprocessed: true, imageSet: imageSet });
      } else {
        console.log("Reprocess failed what now");
      }
    }).catch(function (error) {
      console.log('request failed', error)
    });
  }

  clientId = () => {
    return Math.random().toString(36).slice(2);
  }

  componentDidMount() {
    this.componentWillReceiveProps(this.props);
  }

  componentWillReceiveProps = (nextProps) => {
    const { match } = nextProps;
    //let image = store.imageFromSet();
    console.log(match.params.images, match.params.selected);

    if (match.params.selected == "reprocess") {
      this.reprocess(match.params.images);
    }
  }

  render() {
    const { match } = this.props;
    const { reprocessed, working, problem, error, imageSet } = this.state
    let selection = match.params.selected;

    if (!(selection)) {
      let imageSetParam = match.params.images, images = store.images(imageSetParam);
      if (images != null && images.length > 0) {
        let best = images[0];
        for (let i = 0; i < images.length; i++) {
          if (best.Result.Rank < images[i].Result.Rank) {
            best = images[i];
          }
        }
        selection = best.Id;
      }
    }
    return (
      <div>
        {reprocessed && (
          <Redirect to={"/detail/" + imageSet} />
        )}
        <Header />
        <Analyze imageSet={match.params.images} selected={selection} />
        <div className="container">
          <div className="row">
            <div className="col">
              <h2>Other Evaluated Images &nbsp;&nbsp;<span style={{ fontSize: "12px", fontStyle: "italic" }}>Click to see details</span></h2>
            </div>
          </div>
          <Results howMany={3} imageSet={match.params.images} selected={selection} />
        </div>
        <Footer />
      </div>
    )
  }
}

export default Detail




