import React, { Component } from 'react'
import PropTypes from 'prop-types'
class GaugeR extends Component {
  state = {
    id: ""
  }

  static propTypes = {
    target: PropTypes.string,
    value: PropTypes.number,
    scale: PropTypes.number
  }


  opts = {
    angle: 0,
    lineWidth: 0.20,
    radiusScale: 1,
    pointer: {
      length: 0.34,
      strokeWidth: 0.031,
      color: '#000000'
    },
    limitMax: true,
    limitMin: true,
    generateGradient: true,
    highDpiSupport: true,
    staticZones: [
      { strokeStyle: "#b1242c", min: 0, max: 100 },
      { strokeStyle: "#f5572a", min: 100, max: 200 },
      { strokeStyle: "#f0b245", min: 200, max: 300 },
      { strokeStyle: "#d7db2d", min: 300, max: 400 },
      { strokeStyle: "#33b448", min: 400, max: 500 }
    ],

    animationSpeed: 15
  };

  componentDidMount = () => {
    let marker = ("gauge-" + Math.random()).replace("0.", "");
    this.setState({ id: marker });
  }

  componentWillUpdate() {

  }

  componentDidUpdate = () => {
    const { value, scale } = this.props
    if (scale == 10) {
      opts.staticZones = [
        { strokeStyle: "#b1242c", min: 0, max: 100 },
        { strokeStyle: "#e74435", min: 100, max: 200 },
        { strokeStyle: "#f5572a", min: 200, max: 300 },
        { strokeStyle: "#f49227", min: 300, max: 400 },
        { strokeStyle: "#f0b245", min: 400, max: 500 },
        { strokeStyle: "#faea33", min: 500, max: 600 },
        { strokeStyle: "#d7db2d", min: 600, max: 700 },
        { strokeStyle: "#89ca4a", min: 700, max: 800 },
        { strokeStyle: "#33b448", min: 800, max: 900 },
        { strokeStyle: "#108a49", min: 900, max: 1000 }
      ]
    }
    let gauge = new Gauge(document.getElementById(this.state.id));
    gauge.setOptions(this.opts);
    gauge.maxValue = 500;
    gauge.setMinValue(0);
    gauge.set(value);
  }

  render() {
    const { id } = this.state;
    let { width, height } = this.props;
    width = width ? width : 120;
    height = height ? height : 50;
    return (
      <canvas width={width} height={height} id={id}></canvas>
    )
  }
}

export default GaugeR



