import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Spinner from './Spinner'
import store from './Store'
import { API } from '../Constants'

class Facebook extends Component {
  state = {
    ourToken: null,
    hasUser: false,
    user: null,
    pages: null,
    working: false
  }

  componentDidMount() {

    this.status()
  }

  status = () => {
    console.log("status()")
    let that = this;
    FB.getLoginStatus(function (response) {
      console.log("FB.getLoginStatus", response);
      if (response.status == 'connected') {
        that.fbLoginOK(response.authResponse, true);
        //that.pages(response.authResponse.accessToken);
      }
    });
  }

  busy = (isBusy) => {
    this.setState({ working: (isBusy === false ? false : true) });
  }

  loginWithPageId = (pageId) => {
    return fetch(API + '/user/loginwithpageid?pageid=' + pageId)
      .then(function (response) {
        console.log('request succeeded with JSON response', response)
        if (response.ok) {
          return response.json();
        }
        return false;
      })
      .then((data) => {
        if (data) {
          //this.setState({ ourToken: data.access_token, hasUser: true });
          this.setState({ ourToken: data.access_token });
          //this.update(data.access_token, 0, 150);
          /*
          {"username":"193641857490792_admin",
            "organizationid":"fc77744e-e1ed-44bd-bea5-ada3253044e4",
            "access_token":"vr41wltLi8hCly4fGu5AvBhoFUeH_khQfmU02wMtbX0sDdpTYX-a6q6_puuHodLLSnBjmcjEGi0Jq2sUP3B1hW5KN9J6sgghRcgKV48x5xbCsGtEHXlcl0-CFlXUJO1gUlwDcU7hXnhbpLkGjoqZ_bIKXgXIP62Tr2m6Bvw7txNMbNycUb93FuKjZINR9s9PB10wr8dYzrcpJnRwKrPs9YD_KjyMAYPblDBphsPsa30YA-2qEoFViy4zyiXzUkbSG7BKM6O5g6Nd24lf6KQx8ApvJWNy0ExcN73o1KvyhUa2tSF0Dns58YY2mPUALzYPnQbCre2Tbq4Cg39nGoqU7g",
            "token_type":"bearer",
            "expires_in":"788400000",".issued":"5/17/2017 2:26:48 PM +00:00",".expires":"5/11/2042 2:26:48 PM +00:00"}
        */
        }
        console.log(data);
        return data;
      })
      .catch(function (error) {
        console.log('request failed', error)
        return error;
      });
  }

  login = () => {
    var that = this;
    this.busy();
    FB.login(function (response) {
      if (response.status === 'connected') {
        that.fbLoginOK(response.authResponse);
      } else {
        that.busy(false);
      }
    },
      {
        scope: 'public_profile,email,user_friends,manage_pages,publish_pages,read_insights'
      });
  }

  fbLoginOK = (authResponse, noInteraction) => {
    this.setState({ fbAuth: authResponse });
    let token = authResponse.accessToken;
    this.extendToken(token).then((data) => {
      let accessToken = token;
      if (data) {
        accessToken = data;
        this.setState({ extendedToken: data });
      }
      this.pages(accessToken, noInteraction);
    });
  }

  selectPage = (item) => {
    let user = {
      ourToken: this.state.ourToken,
      token: item.access_token,
      extendedToken: this.state.extendedToken,
      fanCount: item.fan_count,
      pageId: item.id,
      pageName: item.name,
      pageImage: item.picture.data.url,
      id: this.state.fbAuth.userID
    }

    let fanCountChanged = false;
    if (store.hasUser()) {
      console.log("Already have user with id: ", store.user().pageId, user.pageId, user.pageId == store.user().pageId);
      fanCountChanged = store.user().fanCount != user.fanCount;
      console.log("Already have user with fan count: ", store.user().fanCount, user.fanCount);
    }
    //TODO: fan count changed, reprocess

    this.loginWithPageId(item.id).then((data) => {
      if (data) {
        user.organizationId = data.organizationid;
        user.ourToken = data.access_token;
        if (fanCountChanged) {

          this.update(data.access_token, 0, user.fanCount);
        }
        this.setState({ user: user, hasUser: true });
        store.user(user);
        return data;
      }
      return false;
    }).then((hasUser) => {
      console.log("hasUser", hasUser);
      if (!hasUser) {
        this.createUser(user);
      }
    });
  }

  createUser = (user) => {
    console.log("createUser", user);
    this.busy();
    //this.setState({ user: user, hasUser: true });
    //store.user(user);
    this.signup(user.pageName, (user.extendedToken ? user.extendedToken : user.token), user.pageId, user.fanCount)
      .then((data) => {
        console.log(data);
        if (data) {
          user.organizationId = data.Id;
        }
        this.setState({ user: user, hasUser: true });
        store.user(user);
        this.busy(false);
      });
  }

  extendToken = (token) => {
    return fetch('http://onedestapidev.azurewebsites.net/configuration/extendAccessToken?appId=749679035174071&accessToken=' + token, {})
      .then(function (response) {
        if (response.ok) {
          return response.json();
        }
        return false;
      }).catch(function (error) {
        console.log('request failed', error)
        return error;
      });
  }
  signup = (name, token, pageId, fanCount) => {
    let data = {
      'Name': name,
      'PageId': pageId,
      'PageToken': token,
      'PageType': 0,
      'FanCount': fanCount
    }, that = this;

    return fetch(API + '/user/organization', {
      headers: {
        "Content-Type": "application/json; charset=utf-8"
      },
      method: "post",
      body: JSON.stringify(data)
    }).then(function (response) {
      console.log('signup succeeded with JSON response', response);
      if (response.ok) {
        return response.json();
      }
      return false;
    }).catch(function (error) {
      console.log('request failed', error)
    });
  }

  update = (token, id, fanCount) => {
    let data = {
      "FanCount": fanCount
    };
    return fetch(API + '/user/updateorganization', {
      headers: {
        "Authorization": "Bearer " + token,
        "Content-Type": "application/json; charset=utf-8"
      },
      method: "POST",
      body: JSON.stringify(data)
    })
      .then(function (response) {
        return response.ok;
      }).catch(function (error) {
        return error;
      });
  }

  logout = () => {
    FB.logout(function (response) { });
  }

  pages = (longToken, noInteraction) => {
    var that = this;

    function fnNoInteraction(accountsResponse) {
      if (accountsResponse.data) {
        console.log(accountsResponse.data);
        //TODO: Check and update fan count
      }
    }

    function fnInteraction(accountsResponse) {
      if (accountsResponse.data) {
        that.setState({ pages: accountsResponse.data });
      }
      that.busy(false);
    }

    FB.api('me/accounts', 'get', { access_token: longToken, fields: 'picture,fan_count,name,category,access_token' },
      noInteraction ? fnNoInteraction : fnInteraction);
  }

  render() {
    const { pages, working } = this.state;
    return (
      <div style={{ textAlign: "center" }} >
        <button className="btn btn-primary btn-radius fb-connect" onClick={this.login} >
          <i className="fa fa-facebook"></i>&nbsp;&nbsp;&nbsp;&nbsp;CONNECT WITH FACEBOOK</button> <br />
        {working && (<Spinner />)}
        {pages && (
          <div className="dropdown-menu" style={{ position: "static", display: "block" }}>
            <a className="dropdown-item">Select Facebook Page</a>
            <div className="dropdown-divider"></div>
            {pages.map((item, i) => (
              <a className="dropdown-item" key={i} >
                <button type="button" className="btn btn-secondary" onClick={() => this.selectPage(item)}>Use</button>
                &nbsp;&nbsp;
                {item.name} <small>, {item.fan_count} fans</small>&nbsp;&nbsp;</a>
            ))}
          </div>
        )}
      </div>
    )
  }
}
export default Facebook



