import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Loading from './Loading'
import GaugeR from './GaugeR'
import store from './Store'
import { Link } from 'react-router-dom'

import { API, RANKS } from '../Constants'


class Results extends Component {
  state = {
    user: null,
    images: [],
    imageSet: "",
    selected: null
  }

  componentDidMount() {
    this.preload();
    store.addListener(this.storeUpdated, "user");
  }

  componentWillUnmount() {
    store.removeListener(this.storeUpdated, "user");
  }

  storeUpdated = () => {
    if (store.hasUser()) {
      this.setState({ user: store.user() })
    }
  }

  componentWillUpdate() {
  }

  preload() {
    const { howMany, imageSet, selected } = this.props;
    let images = store.images(imageSet) == null ? [] : store.images(imageSet),
      best = null;
    for (let i = 0; i < images.length; i++) {
      if (images[i].Id == selected) {
        best = images[i];
        break;
      }
    }
    if (store.hasUser()) {
      this.setState({ user: store.user() })
    }

    this.setState({ images: images, selectedImg: best });
  }

  render() {
    const { howMany, imageSet, selected } = this.props;
    const { images, user } = this.state;
    return (
      <div className="row">
        {images ? (
          images.map((item, i) => (
            <Box key={item.Id}
              itemId={item.Id}
              contentId={"/detail/" + imageSet + "/" + item.Id}
              url={item.Url}
              rank={item.Result.Rank * 100}
              strength={RANKS[item.Result.Rank]}
              interactionCount={user != null ? item.Result.EstimatedInteractionCount : 0}
              selected={selected}
              scale={item.Result.Scale}
            />
          ))
        ) : (
            <Loading />
          )}

        {images.length == 1 && (
          <div className="col-sm"></div>
        )}
      </div>
    )
  }
}

Results.propTypes = {
  howMany: PropTypes.number,
  imageSet: PropTypes.string
}

const Box = ({ itemId, contentId, url, rank, strength, fanBase, interactionCount, selected, scale }) => (
  <div className="col-sm">
    <div className="box with-radius with-shadow" style={{ border: selected === itemId ? "1px solid #e4405f" : "" }}>
      <Link to={contentId}><div className="f-img" style={{ backgroundImage: `url(${url})` }} ></div></Link>
      <div className="box-info row align-items-center">
        <div className="col-4">
          <GaugeR value={rank} range={scale} />
        </div>
        <div className="col-8 box-info-details">
          <span>Strength <b>{strength}</b></span>
          <span style={{ display: "none" }}>Fan Base <b>{fanBase}</b></span>
          <span style={{ display: interactionCount > 0 ? "inline-block" : "none" }}>Estimated Likes <b>{interactionCount}</b></span>
        </div>
      </div>
    </div>
  </div>
)

Box.propTypes = {
  itemId: PropTypes.string,
  url: PropTypes.string,
  strength: PropTypes.string,
  rank: PropTypes.number,
  fanBase: PropTypes.number,
  interactionCount: PropTypes.number,
  contentId: PropTypes.string,
  selected: PropTypes.string,
  scale: PropTypes.number
}

export default Results


