import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { API, RANKS } from '../Constants'
import Loading from './Loading'
import GaugeR from './GaugeR'


class RecentImages extends Component {
  state = {
    recents: []
  }

  componentDidMount() {
    this.preload()
  }

  preload() {
    const { howMany } = this.props;
    var that = this;
    fetch(API + '/page/evaluations', {})
      .then(function (response) {
        if (response.ok)
          return response.json();
        return [];
        //console.log('request succeeded with JSON response', data)
      }).then(function (data) {
        that.setState({ recents: data.slice(0, howMany) });
      }).catch(function (error) {
        console.log('request failed', error)
      });

  }

  render() {
    const { recents } = this.state
    return (
      <div className="row align-items-center">
        {recents ? (
          recents.map((item, i) => (
            <Box key={i} url={item.Url} rank={item.Result.Rank * 100}
              strength={RANKS[item.Result.Rank]}
              interactionCount={item.Result.InteractionCount} />
          ))
        ) : (
            <Loading />
          )}
      </div>
    )
  }
}

RecentImages.propTypes = {
  howMany: PropTypes.number
}

const Box = ({ url, rank, strength, fanBase, interactionCount }) => (
  <div className="col-sm " >
    <div className="box with-radius with-shadow">
      <div className="f-img" style={{ backgroundImage: `url(${url})` }} ></div>
      <div className="box-info row align-items-center">
        <div className="col-4">
          <GaugeR value={rank} />
        </div>
        <div className="col-8 box-info-details">
          <span>Strength <b>{strength}</b></span>
          <span>&nbsp;</span>
          <span style={{ display: "none" }}>Fan Base <b>{fanBase}</b></span>
          <span style={{ display: "none" }}>Estimated Likes <b>{interactionCount}</b></span>
        </div>
      </div>
    </div>
  </div>
)

Box.propTypes = {
  url: PropTypes.string,
  strength: PropTypes.string,
  rank: PropTypes.number,
  fanBase: PropTypes.number,
  interactionCount: PropTypes.number
}

export default RecentImages


