import React from 'react'
import { Link } from 'react-router-dom'

const Header = () => (
  <header className="navbar navbar-alt navbar-default navbar-toggleable-md bd-navbar">
    <nav className="container">
      <div className="d-flex justify-content-between hidden-lg-up">
        <a className="navbar-brand" href="/">
          <img src="/logo-alt.png" />
        </a>
        <button className="navbar-toggler" type="button">
          <span className="navbar-toggler-icon"></span>
        </button>
      </div>

      <div className="collapse navbar-collapse" id="bd-main-nav">
        <ul className="nav navbar-nav">
          <li className="nav-item active">
            <a className="nav-item nav-link " href="/"><img src="/logo-alt.png" /></a>
          </li>
        </ul>
      </div>
    </nav>
  </header>
)

export default Header



