import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Block } from 'jsxstyle'
import { API } from '../../Constants'
import Instagram from '../Instagram'
import GaugeR from '../GaugeR'
import Facebook from '../Facebook'

import store from '../Store'


class Analyze extends Component {

  state = {
    testing: false,
    hasUser: false,
    user: { pageName: "Unknown", id: 0 },
    image: { Url: "", Result: { Rank: 0, Scale: 5 } }
  }

  handleClick = (e) => {
    this.setState({ testing: !this.state.testing });
  }

  componentDidMount() {
    store.addListener(this.storeUpdated, "user");
    this.componentWillReceiveProps(this.props);
  }

  componentWillUnmount() {
    store.removeListener(this.storeUpdated, "user")
  }

  componentWillReceiveProps = (nextProps) => {

    let user = this.state.user, hasUser = this.state.hasUser;
    if (user.id == 0 && store.hasUser()) {
      user = store.user();
      hasUser = true;
    }

    const { selected, imageSet } = nextProps;
    let image = store.imageFromSet(imageSet, selected);
    if (image != null) {
      this.updateUserRelatedData({ image: image, user: user, hasUser: hasUser });
    }
  }

  storeUpdated = () => {
    this.updateUserRelatedData({ user: store.user(), hasUser: true, image: this.state.image }, true);
  }

  updateUserRelatedData = (data) => {
    let image = data.image;
    if (data.hasUser) {
      image.Result.FanCount = data.user.fanCount + "";
      image.Result.Performance = 100 * (image.Result.EstimatedInteractionCount / image.Result.MaxInteractionCount);
      image.Result.Likes = image.Result.MinInteractionCount + " - " + image.Result.MaxInteractionCount + " (Out of " + image.Result.OutOf + ")";
    } else {
      image.Result.Performance = 0;
      image.Result.FanCount = "Unknown";
      image.Result.Likes = "Unknown";
    }

    this.setState(data);
  }

  render() {
    const { user, hasUser, testing, image } = this.state;
    return (
      <div className="container" id="result_start">
        <div className="row">
          <div className="col-sm">
            <div className="box with-border">
              <div className="img-detail row ">
                <div className="col-2">
                  {user.pageImage ? (
                    <img src={user.pageImage} style={{ maxWidth: "50px" }} />
                  ) : (
                      <i className="fa fa-user-circle-o" style={{ fontSize: 35, color: "#335698", border: "1px solid #ccc", padding: "5px 5px" }}></i>
                    )}
                </div>
                <div className="col-8" style={{ textAlign: "left" }}>
                  <a href={"https://www.facebook.com/" + user.pageId} target="_blank">{user.pageName}</a><br />
                  <small>0 mins</small>
                </div>
                <div className="col-2" style={{ textAlign: "right" }}>
                  <button onClick={this.handleClick} className="btn btn-secondary btn-sm" style={{ marginRight: "0" }}><i className="fa fa-paw"></i></button>
                </div>
              </div>
              <div className="img-detail row align-items-center">
                <div className="col">
                  <img src={image.Url} />
                </div>
              </div>
              <div className="img-results row align-items-center" style={{ display: "none" }}>
                <div className="col-6" ><small>0 likes</small>&nbsp;&nbsp; <small>&bull; 0 Comments</small></div>
                <div className="col-6">
                  --
                </div>
              </div>
            </div>
          </div>
          <div className="col-sm">
            <div className="box with-border analyze-results">
              <h3>Strength</h3>
              <div className="box-results row">
                <div className="col-4" style={{ margin: 0 }}>
                  <GaugeR value={image.Result.Rank * 100} width={135} height={70} />
                </div>
                <div className="col-8">
                  Your image is rated as <br />
                  <b style={{ fontSize: "130%" }}>{image.Result.Rank}</b>/ {image.Result.Scale}
                </div>
              </div>
              <div className="line"></div>
              <h3>Estimated Results </h3>
              <div className="box-results row" >
                <div className="col">
                  <b>Fan base</b> <br /> {image.Result.FanCount}<br /><br />
                  <b>Likes</b> <br /> {image.Result.Likes} <br /><br />
                  <div style={{ marginBottom: "25px" }} className="progress">&nbsp;
                    <div className="progress-inc" style={{ height: image.Result.Performance + "%" }}></div>
                  </div>
                  <div style={{ display: (hasUser && !testing) ? "none" : "" }}>
                    <Facebook /> <br />
                    <Instagram />
                    <p style={{ textAlign: "center" }}>
                      <span>to learn your number of likes</span></p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div >
    )
  }
}

Analyze.propTypes = {
  selected: PropTypes.string,
  imageSet: PropTypes.string
}

export default Analyze



