import React from 'react'
import { Block } from 'jsxstyle'
import Header from './Header'
import Intro from './Intro'
import Footer from '../Footer'
import RecentImages from '../RecentImages'

export default () => (
  <Block>
    <Header />
    <Intro />
    <div className="container">
      <div className="row">
        <div className="col">
          <h2>Recently Evaluated Images</h2>
        </div>
      </div>
      <RecentImages howMany={3} />
      <div className="row">
        <div className="col">
          <h2>How It Works?</h2>
        </div>
      </div>
      <div className="row">
        <div className="col-sm-12">
          <div className="row align-items-center box bordered with-shadow features">
            <div className="col-sm">
              <img src="/collect.svg" />
              <h3>Collects</h3>
              <p>Crawls more than 100 Facebook Travel and Lifestyle pages. Collects all page, post and engagement data. </p>
            </div>
            <div className="col-sm">
              <img src="/tag.svg" />
              <h3>Tags and Analyze</h3>
              <p>Tags each one of the posts according to image, text, time and context features. Analyze performances of posts.

            </p>
            </div>
            <div className="col-sm">
              <img src="/predict.svg" />
              <h3>Predicts</h3>
              <p>Runs deep learning algorithms to predict your posts engagement rate and calculates your numbers according to
              your page data.</p>
            </div>
          </div>
        </div>
      </div>
      <div className="row">
        <div className="col">
          <h2>Our References</h2>
        </div>
      </div>
    </div>
    <Footer />
  </Block>
)




